@import "../../../../bootstrap";

.team-person-card {
    height: auto !important;


    @media ($breakpoint_md_max) {
        $width: 70vw;
        $max-width: 234px;
        width: $width !important;
        max-width: $max-width !important;

        & img {
            height: calc(#{$width} / .6) !important;
            max-height: calc(#{$max-width} / .6) !important; } }


    @media ($breakpoint_lg) {
        $width: 185px;
        width: $width !important;

        & img {
            height: calc(#{$width} / .6) !important; } }


    @media ($breakpoint_xl) {
        $width: 210px;
        width: $width !important;

        & img {
            height: calc(#{$width} / .6) !important; } }


    & img {
        display: block;
        border-radius: 3px; } }
